// logout.js
import { useEffect } from "react";
import { useAuth } from "../context/authContext";
import withRouter from "../hoc/withRouter";

const Logout = () => {
  const { logout } = useAuth();
  const { navigate } = this.props;

  useEffect(() => {
    logout();
    navigate("/");
  }, [logout, navigate]);

  return null;
};

export default withRouter(Logout);
